import { useEffect, useState } from "react";
import { NavLink } from "react-router-dom";
import { BurgerMenu } from "./_partials";

export default function Header() {
  const [time, setTime] = useState(new Date());

  const nav_about = [
    {"title": "Mon profil", "lien": "/aboutme/presentation"},
    {"title": "Mes trophes", "lien": "/trophies"},
    {"title": "Dconnexion", "lien": "/logout"},
  ]
  
  const nav_home = [
    {"title": "Portfolio", "lien": "/"},
    {"title": "Contact", "lien": "/contact"},
  ]

  useEffect(() => {
    const timer = setInterval(() => {
      setTime(new Date());
    }, 1000);
    return () => clearInterval(timer);
  }, []);

  return (
    <>
      <header>
        {/* Navigation */}
        <nav>
          {nav_home.map((element) => {
            return (
              <NavLink
                to={element.lien}
                end
                className={({isActive}) => (
                  "nav-link " + (isActive ? "active" : "")
                )}
              >
                {element.title}
              </NavLink>
            )
          })}
        </nav>

        {/* Profil + Heure */}
        <div>
          <BurgerMenu type="profile" data={nav_about} title="Matto Kervadec">
            <img
              className="profil-icon"
              src="/image/matteo_kervadec.png"
              alt="Icon de Matto Kervadec"
              title="Icon de Matto Kervadec"
            />
          </BurgerMenu>
          <time
            dateTime={`${time.getHours().toString().padStart(2, "0")}:${time.getMinutes().toString().padStart(2, "0")}`}
          >
            {time.getHours().toString().padStart(2, "0")}:{time.getMinutes().toString().padStart(2, "0")}
          </time>
        </div>
      </header>
    </>

  );
}